.nav{
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 1;

    /* animation */
    transition-timing-function: ease-in-out;
    transition: all 0.5s;
    
}


.nav_contents{
    display: flex;
    justify-content: space-between;
}

.nav_black{
    background: #111;
}


.nav_logo{
    position: fixed;
    top: 11px;
    right: 20px;
    left: 0;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
    
}


.nav_avatar{
    position: fixed;
    top: 11px;
    right: 20px;
    width: 30px;
    height: 30px;
    margin-top: 8px;
    cursor: pointer;
}