.Banner{
    color: white;
    position: relative;
    height: 400px;
    width: 100%;
    /* background-image: url("https://thumbs.dreamstime.com/b/netflix-logo-laptop-screen-photograph-dallas-texas-united-states-computer-116964177.jpg"); */
    background-position:center center;
    background-size: cover;
}

.banner_contents{
    margin-left: 30px;
    padding-top: 140px;
    height: 190px;
}

.banner_title{
    font-size: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;

}

.banner_description{
    width:45rem;
    line-height: 1.3rem;
    padding-top: 1rem;
    font-size: 0.8rem;
    max-width: 360px;
    height: 80px;
   
}

.banner--fadebottom{
    height: 4.4rem;
    background-image: linear-gradient(180deg , transparent , rgba(37,37,37,0.61),#111);
}

.banner_button{
    cursor: pointer;
    color: #fff;
  

    outline:none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding-left: 2rem;
    padding-right: 2rem;
    margin-right: 1rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    background-color: rgba(51, 51, 51, 0.5);
    
}

.banner_button:hover{
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.4s;
}